import Vue from 'vue'
import Router from 'vue-router'
import UserManage from '@/components/userManage'
import DeviceManage from '@/components/deviceManage'
import AuthorityManage from '@/components/authorityManage'
import RoleManage from '@/components/authorityManage/children/roleManage'
import Priority from '@/components/authorityManage/children/priority'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'usermanage',
      component: UserManage
    },
    {
      path: '/devicemanage',
      name: 'devicemanage',
      component: DeviceManage
    },
    {
      path: '/authoritymanage',
      name: 'authoritymanage',
      component: AuthorityManage,
      children: [
        {
          path: 'rolemanage',
          name: 'rolemanage',
          component: RoleManage
        },
        {
          path: 'priority',
          name: 'priority',
          component: Priority
        }
      ]
    }
  ]
})



