<template>
    <div id="app">
      <header>
        <nav class="main_nav">
          <el-row>
            <el-col :span="16">
              <img src="./assets/logo.png" alt="">
              <h1>融合通信资源管理平台</h1>
            </el-col>
            <el-col :span="8">
              <el-menu 
                theme="dark" 
                default-active="usermanage" 
                class="nav_menu" 
                mode="horizontal"
                @select="navSelectHandler" 
              >
                <el-menu-item index="usermanage">用户管理</el-menu-item>
                <el-menu-item index="devicemanage">设备管理</el-menu-item>
                <el-submenu index="authority">
                  <template slot="title">权限管理</template>
                  <el-menu-item index="rolemanage">角色管理</el-menu-item>
                  <el-menu-item index="priority">呼叫优先级管理</el-menu-item>
                </el-submenu>
              </el-menu>
            </el-col>
          </el-row>
        </nav>
      </header>
      <main>
        <router-view></router-view>
      </main>
    </div>
</template>


<script>
export default {
    name: 'app',
    methods: {
      navSelectHandler(val){
        this.$router.push({
          name:val
        })
      }
    }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
a{
  text-decoration:none;
  display:block;
}
html{
  background:url(./assets/bg.jpg) no-repeat center/ cover;
}
.main_nav{
  min-width:1200px;
  margin: 0 auto;
  padding:5px;
  border-radius:5px;
  background:rgba(255,255,255,.2);
}
.main_nav img{
  display:block;
  height:60px;
  float:left;
}
h1{
  float:left;
  margin:0;
  color:#bfcbd9;
  height:60px;
  line-height:60px;
}
.nav_menu{
  background:rgba(0,0,0,0);
}
main{
  min-width:1200px;
  margin:0 auto;
}
</style>



